.StandardModal {
  --transition: opacity 0.2s,
                z-index 0.2s;
}
.StandardModal .window {
  width: 28rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 0.25rem solid var(--border);
  border-radius: 0.5rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.1);
  background-color: var(--background);
  transition: var(--transition);
}
.StandardModal .window .header {
  padding: 1.5rem;
  border-bottom: 1px solid var(--border);
  font-size: 1.125rem;
}
.StandardModal .window .main {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 1.5rem;
}
.StandardModal .window .flex-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0.5rem;
}
.StandardModal .overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(3px);
  transition: var(--transition);
}
.StandardModal .invisible {
  z-index: -1;
  opacity: 0;
}
.StandardModal .visible {
  z-index: 10000;
  opacity: 1;
}

