.invisible {
  opacity: 0;
}

.create-post-section .create-post-form {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}
.create-post-section .create-post-form .create-post-form__main {
  display: flex;
  flex-direction: column;
  gap: 0.375rem;
  align-items: flex-start;
}
.create-post-section .create-post-form .create-post-form__main .flex-container {
  width: 100%;
  display: flex;
  gap: 0.5rem;
  justify-content: space-between;
  align-items: flex-start;
}
.create-post-section .create-post-form .create-post-form__main .flex-container .create-post-form__buttons--flex-container {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  gap: 0.25rem 0.5rem;
  width: 100%;
  justify-content: flex-end;
}

