.StandardButton {
  user-select: none;
  padding: 0.5rem 2rem;
  border: 0.125rem solid var(--border);
  border-radius: 0.5rem;
  color: var(--text);
  line-height: 1;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 0.05rem;
}
.StandardButton:hover {
  opacity: 0.8;
}
.StandardButton[data-type=submit] {
  background-color: #73e69d;
}
.StandardButton[data-type=cancel] {
  background-color: #f47561;
}

